<template>
  <div class="login" v-show="!hasLogin">
      <!-- 手机号码-->
        <van-field v-model="phone"  label="手机号" placeholder="请输入手机号"/>
      <!-- 手机号码-->

      <!-- 短信验证码-->
      <van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
            <template #button>
              <van-button size="small" type="primary" @click="clickCodeBtn">
                <span v-show="ws_sendCode == false && theRestOfTime == 0">发送验证码</span>
                <span v-show="ws_sendCode == true">正在发送中</span>
                <van-count-down :time="theRestOfTime" format="ss 秒"
                v-show='theRestOfTime>0' @finish="timeFinish"/>
              </van-button>
            </template>
      </van-field>
      <!-- 短信验证码-->

     <!-- 登录或者注册-->
      <van-button type="primary" size="large" @click="login" class='click'>
          <div v-show="!ws_login">点此登录</div>
          <div v-show="ws_login">登录中. . .</div>
      </van-button>
      <!-- 登录或者注册-->

      <!-- 图形拼图-->
      <PuzzleVerification v-model="PVFShow" :onSuccess="PVFSuccess" class="am-puzzle"
      width = '260' height = '120' blockType='puzzle':puzzleImgList="puzzleImgList"/>
      <!-- 图形拼图-->


  </div>
</template>

<script>
  import PuzzleVerification from 'vue-puzzle-verification'
  // vuex
  // import {mapMutations,createNamespacedHelpers} from 'vuex'
  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  export default{
    name:"login",
    /* 数据*/
    data(){
      return {
        /* 输入框值*/
        // inputVal : '',
        /* 登录*/
        ws_login : false,
        /* 手机号码*/
        phone : '',
        // 短信验证码
        sms : '',
        /* 已经发送验证码*/
        ws_sendCode : false,
        /* 展示拼图组件*/
        PVFShow : false,
        /* 拼图图片链接*/
        puzzleImgList : ['http://img4.imgtn.bdimg.com/it/u=2983887807,22279237&fm=26&gp=0.jpg'],
        /* 剩余时间*/
        theRestOfTime :   0,
      }
    },
    /* 方法*/
    methods:{
      // 定时器完成了
      timeFinish(){
        this.theRestOfTime = 0
      },
      /* 拼图成功 发起请求*/
      PVFSuccess(){
        let params = {
          phoneNum : this.phone
        }
        /* 隐藏拼图*/
        this.PVFShow =false
        /* 请求*/
        this.$api.login.sendCode(params).then(
            res =>{
              this.ws_sendCode = false
                if(res.data == 'success'){
                    this.$toast('验证码发送成功,请查收!')
                    this.theRestOfTime = 60 * 1000;
                }else if(res.data == 'fail'){
                    this.$toast('验证码发送失败,请稍后重试!')
                }
            }
        )

        /* 测试*/
        // setTimeout(()=>{
        //   this.ws_sendCode = false
        //   this.theRestOfTime = 60 * 1000;
        //   alert(this.ws_sendCode)
        // },1000)

      },
      //点击发送验证码按钮
      clickCodeBtn(){
        /* 手机号是否合法 [34578]是第二位数字*/
        let phoneReg = /^1[34578]\d{9}$/;
        if(!phoneReg.test(this.phone)){
          this.$toast("手机号码格式错误")
          return;
        }
        /* 定时未结束*/
        if(this.theRestOfTime > 0){
          this.$toast("请等待60秒")
          return
        }
        /* 正在发送验证码*/
        if(this.ws_sendCode == true){
          this.$toast("验证码发送中...")
          return
        }
        this.ws_sendCode = true
        /* 显示拼图*/
        this.PVFShow =true
      },
      /* ********************************8*/
      // 输入事件
      // input(){
      //   // this.inputVal=this.inputVal.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g,'')
      //   /* 将后面的 替换前面匹配的  不需要完全匹配*/
      //   // this.inputVal=this.inputVal.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5]/g,'')
      //   this.inputVal=this.inputVal.replace('1','')
      //   console.log(this.inputVal,this.inputVal.length)
      // },
      // ...mapMutations({
      // 	setTokenLoginCount : 'setTokenLoginCount'
      // }),
      ...login.mapMutations({
            	HasTokenThenFirstHandler : 'HasTokenThenFirstHandler'
            }),
      ...login.mapMutations({
              hasLoginHandler: 'hasLoginHandler'
            }),
      ...login.mapMutations({
              userInfoHandler: 'userInfoHandler'
            }),
      // 登录按钮////////////////////////////////////////////////////////////////////////
      login(){
        /* 手机号是否合法 [34578]是第二位数字*/
        let phoneReg = /^1[34578]\d{9}$/;
        if(!phoneReg.test(this.phone)){
          this.$toast("手机号码格式错误")
          return;
        }
        /* 验证码是否合法 */
        let smsReg = /^\d{6}$/;
        if(!smsReg.test(this.sms)){
          this.$toast("验证码是六位纯数字")
          return;
        }
        /* 正在登录*/
        if(!!this.ws_login == true){
          this.$toast("正在登录中...")
          return;
        }
        this.ws_login = true

        var params = {
          /* 手机号码*/
          phoneNum : this.phone,
          /* 验证码*/
          phone_code : this.sms
        }
        this.$api.login.userLogin(params).then(
            res =>{
              this.ws_login = false
                switch(res.data){
                  case "no_phone_num":
                      this.$toast('该手机未请求验证码');
                      break;
                  case "code_err":
                      this.$toast('验证码错误');
                      break;
                  case "code_overdue":
                      this.$toast('验证码过期');
                      break;
                  case "No_information":
                      this.$toast('没有此id信息');
                      break;
                  default :
                      // 本地储存设置token
                      localStorage.setItem('token', res.data.token)
                      // 设置登录状态为true
                      this.hasLoginHandler(true)
                      //设置第一次进入网页为false
                      this.HasTokenThenFirstHandler(false)
                      this.userInfoHandler(res.data.result)
                      this.$toast('登录成功!!')
                      this.$router.replace({
                        path:'/user'
                      })
                }
            }
        )
      }
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      PuzzleVerification
    },
    /* 计算*/
    computed:{
     ...login.mapState({
          	hasLogin : state => state.hasLogin
          })
    },
    /* 挂载阶段*/
    mounted() {
        /* 如果用户登录则跳转到用户页*/
        if(this.hasLogin){
          this.$router.replace({
            path:'/user'
          })
        }
    },
    // 监听
    watch:{

      // code: {
      //     handler(newName, oldName) {
      //     var codeReg = /^[\d]{6}$/;
      //     if(!codeReg.test(this.code)){
      //       this.errCode = '验证码是六位的纯数字'
      //     }else{
      //       this.errCode = ''
      //     }
      //     },
      //     // immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style>
  /* 新的*/
   /* /deep/.puzzle-container { display: block !important; } */
  /* 新的*/
  /* 计时器 字体颜色*/
 .van-button__text>.van-count-down{
    color: white;
  }
</style>
<style scoped lang="scss">
  .login{
    position: relative;
    @include wh(700px,100%);
    margin: 100px auto 0;


    .click{
      margin-top: 100px;
    }
  }
</style>



